.form {
  &__field {
    width: 100%;
    height: 48px;
    border: none;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 14px 16px;
    font-family: $font-family;
    background-color: #f9f9f9;

    &::placeholder {
      color: #7e7e83;
    }

    &:hover {
      border: 1px solid #dbdbdb;

      &::placeholder {
        color: $main-text-color;
      }
    }

    &:focus {
      border: 1px solid #F98921;
      outline: none;
    }

    &--message {
      height: 148px;
      resize: none;
    }
  }
}
